<template>
    <div style="padding-top: 2px;">
        <el-form :inline="true" ref="searchFormRef" :model="searchFormData" size="mini">
            <el-form-item label="项目" prop="project">
                <el-input v-model="searchFormData.project" placeholder="请输入项目"></el-input>
            </el-form-item>
            <el-form-item label="项目名称" prop="project_name">
                <el-input v-model="searchFormData.project_name" placeholder="请输入项目名称"></el-input>
            </el-form-item>
            <el-form-item label="创建时间" prop="createTime">
                <el-date-picker
                        v-model="searchFormData.createTime"
                        type="daterange"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期">
                </el-date-picker>
            </el-form-item>
            <div class="v-search-opt">
                <div class="v-pagination">
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="searchFormData.pageNum"
                            :page-size="searchFormData.pageSize"
                            :total="searchFormData.pageCount"
                            :page-sizes="[16, 30, 50, 100]"
                            layout="total, sizes, prev, pager, next, jumper"
                    >
                    </el-pagination>
                </div>
                <div class="v-inline">
                    <el-button size="mini" type="primary" @click="onSearch"><i class="iconfont icon-chaxun"></i> 查询</el-button>
                    <el-button size="mini" type="primary" @click="onReset"><i class="iconfont icon-chongzhi"></i> 重置</el-button>
                    <el-button size="mini" type="primary" @click="on_recreate_service_check"><i class="iconfont icon-chongxinshengcheng"></i> 重新生成检查服务</el-button>
                    <!--<el-button size="mini" type="primary" @click="onRestart"><i class="iconfont icon-chongzhi"></i> 重新启动</el-button>-->
                    <!--<el-button size="mini" type="primary" @click="onDel"><i class="iconfont icon-chongzhi"></i> 删除</el-button>-->
                    <!--<el-button size="mini" type="primary" @click="onAdd"><i class="iconfont icon-chongzhi"></i> 添加任务</el-button>-->
                </div>
            </div>
        </el-form>
        <hr>
        <el-table :data="tableData" :border="true" :stripe="true" size="mini" :height="getTableHeight(130)">
            <el-table-column fixed="left" type="index" :index="onIndexMethod(searchFormData.pageNum,searchFormData.pageSize)" label="序号" width="60"></el-table-column>
            <el-table-column fixed="left" label="操作" width="200">
                <template slot-scope="scope">
                    <el-col :span="10">
                        <el-button @click.native.prevent="on_run_project_service_check_task(scope.$index, tableData)" type="primary" size="mini">运行检测</el-button>
                    </el-col>
                    <el-col :offset="2" :span="10">
                        <el-button @click.native.prevent="onView(module_name,scope.$index, tableData)" type="primary" size="mini">运行详情</i></el-button>
                    </el-col>
                </template>
            </el-table-column>
            <el-table-column
                    fixed="left"
                    prop="project_service_state_code_id"
                    label="项目服务运行状态"
                    width="120">
                <template slot-scope="scope">
                    <a><i :class="'iconfont icon-dian '+format_project_service_state_style(scope.row,'project_service_state_code_id')"></i>{{"    " + format_project_service_state(scope.row)}}</a>
                </template>
            </el-table-column>
            <el-table-column
                    fixed="left"
                    prop="project_container_status"
                    label="项目服务运行情况"
                    width="180">
            </el-table-column>
            <el-table-column
                    fixed="left"
                    :formatter="format_project_state"
                    prop="project_state_code_id"
                    label="项目运行状态"
                    width="110">
            </el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="project"
                    label="项目"
                    width="180">
            </el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="project_name"
                    label="项目名称"
                    width="180">
            </el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    prop="project_context"
                    label="项目上下文"
                    width="110">
            </el-table-column>
            <el-table-column
                    prop="createTime"
                    label="创建时间"
                    width="156">
            </el-table-column>
            <el-table-column
                    :formatter="format_project_type"
                    prop="project_type_code_id"
                    label="项目类型"
                    width="110">
            </el-table-column>
            <el-table-column
                    :formatter="format_project_env"
                    prop="project_env_code_id"
                    label="项目环境"
                    width="110">
            </el-table-column>
            <el-table-column
                    prop="project_version"
                    label="项目版本"
                    width="110">
            </el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    :formatter="format_project_team"
                    prop="project_team_id"
                    label="项目团队">
            </el-table-column>

        </el-table>
    </div>
</template>

<script>
    export default {
        name: 'serviceCheckList',
        data() {
            return {
                module_name: 'serviceCheck',
                refresh_page_method: 'refresh_' + this.module_name,
                project_type_code_list: null,
                project_env_code_list: null,
                project_state_code_list: null,
                service_state_code_list: null,
                project_team_code_list: null,
                searchFormData: {
                    pageNum: 1,
                    pageSize: 16,
                    pageCount: 1,
                    project: '',
                    project_name: '',
                    createTime: '',
                    order: '+project'
                },
                tableData: []
            }
        },
        created() {
            this.$store.state.Apps.$on(this.refresh_page_method, () => {
                this.search();
            });
        },
        async mounted() {
            this.project_type_code_list = await this.$setCodeList(this.project_type_code_list, 'project_type');
            this.project_env_code_list = await this.$setCodeList(this.project_env_code_list, 'project_env');
            this.project_state_code_list = await this.$setCodeList(this.project_state_code_list, 'project_state');
            this.project_service_state_code_list = await this.$setCodeList(this.project_service_state_code_list, 'project_service_state');
            let project_team_code_list_res = await this.$exec({}, 'OAMP_search_project_team_code_list');
            if (project_team_code_list_res != null && project_team_code_list_res.data != null)
                this.project_team_code_list = project_team_code_list_res.data;
            this.search();
        },
        methods: {
            handleSizeChange(val) //修改单页显示条数
            {
                this.searchFormData.pageSize = val;
                this.search();
            },
            handleCurrentChange(val) //当前页修改
            {
                this.searchFormData.pageNum = val;
                this.search();
            },
            onReset() // 重置功能
            {
                this.$refs.searchFormRef.resetFields();
                this.search();
            },
            format_project_type(row, column, cellValue) /*格式化项目类型信息*/ {
                return this.$formatCodeValue(row, this.project_type_code_list, 'project_type_code_id')
            },
            format_project_env(row, column, cellValue)/*格式化项目环信息*/ {
                return this.$formatCodeValue(row, this.project_env_code_list, 'project_env_code_id')
            },
            format_project_state(row, column, cellValue)/*格式化项目状态信息*/ {
                return this.$formatCodeValue(row, this.project_state_code_list, 'project_state_code_id')
            },
            format_project_service_state_style(row, key) {
                let code = row[key];
                if (code === '1')
                    return 'set-info';
                else if (code === '-1')
                    return 'set-warning';
                else if (code === '0')
                    return 'set-danger';
                else return 'set-black';
            },
            format_project_service_state(row, column, cellValue)/*格式化项目服务运行状态信息*/ {
                return this.$formatCodeValue(row, this.project_service_state_code_list, 'project_service_state_code_id')
            },
            format_project_team(row, column, cellValue)/*格式化项目团队信息*/ {
                return this.$formatCodeValue(row, this.project_team_code_list, column.property, '_id', 'project_team_name')
            },
            async r_deleteProjectInfo(rowData) // 删除项目信息
            {
                let R = new this.$http();
                let {data} = await R.post(rowData, 'OAMP_delete_ProjectInfo');
                return data != null && data !== 'null';
            },
            onSearch() // 查询按钮
            {
                this.searchFormData.pageNum = 1;
                this.search();
            },
            async search() {
                let searchData = this.searchFormData;
                let data = await this.$search(searchData, 'OAMP_search_project_list');
                this.searchFormData.pageCount = data.pageCount;
                this.tableData = data.data
            },
            onDelete(index, rows)  // 删除
            {
                let rowData = rows[index];
                if (this.r_deleteProjectInfo(rowData)) {
                    rows.splice(index, 1);
                    this.$message.success('删除数据成功');
                    this.search();
                }
                else {
                    this.$message.error('删除数据失败');
                }
            },
            on_project_configure(index, rows)  // 项目配置
            {
                this.$router.push({
                    name: 'projectConf',
                    params: {
                        projectId: rows[index]['_id']
                    }
                });
            },
            async on_recreate_service_check()/*重新生成检查服务*/ {
                let item = {};
                let data = await this.$exec(item, 'OAMP_recreate_service_check');
                if (data != null && data.success === '1')
                    this.$message.success('重新生成检查服务成功');
                else
                    this.$message.warning('重新生成检查服务异常');
            },
            async on_run_project_service_check_task(index, rows)/*运行项目服务检查*/ {
                let item = {
                    service_project_id: rows[index]._id
                };
                let data = await this.$exec(item, 'OAMP_run_service_check_task');
                if (data != null && data.success === '1') {
                    this.$message.success('运行项目服务检查成功')
                } else
                    this.$message.warning('运行项目服务检查异常')
                this.search()
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



