<template>
    <el-dialog slot="title"
               :title='"【"+pageData.project_name+"】项目服务检查运行详情"'
               width="1460px"
               :visible="pageInfo.isShow"
               :modal-append-to-body="false"
               :close-on-click-modal="false"
               :center="true"
               :lock-scroll="false"
               @close="onClose(module_name)"
    >
        <el-form :inline="true" ref="searchFormRef" :model="searchFormData" size="mini">
            <div class="v-center">
                <div class="v-inline">
                    <el-button size="mini" type="primary" @click="onSearch"><i class="iconfont icon-chaxun"></i> 查询</el-button>
                    <el-button size="mini" type="primary" @click="on_run_all_service_check_task"><i class="iconfont icon-jiance"></i> 检测全部</el-button>
                    <el-button size="mini" type="primary" @click="on_recreate_service_check_task"><i class="iconfont icon-chongxinshengcheng"></i> 重新生成检查规则</el-button>
                    <el-button size="mini" type="primary" @click="onClose(module_name)"><i class="iconfont icon-guanbi"></i> 关闭</el-button>
                </div>
            </div>
        </el-form>
        <hr>
        <el-table :data="tableData" class="tb-edit" :border="true" :stripe="true" size="mini" :height="getTableHeight(330)" highlight-current-row>
            <el-table-column fixed="left" type="index" :index="onIndexMethod(searchFormData.pageNum,searchFormData.pageSize)" label="序号" width="60"></el-table-column>
            <el-table-column fixed="left" label="操作" width="160">
                <template slot-scope="scope">
                    <el-col :span="12">
                        <el-button @click.native.prevent="on_run_service_check_task(scope.$index, tableData)" type="success" size="mini">运行</el-button>
                    </el-col>
                    <el-col :span="12" v-if="scope.row.service_check_state_code_id === '1'">
                        <el-button @click.native.prevent="on_pause_service_check_task(scope.$index, tableData)" type="info" size="mini">暂停</el-button>
                    </el-col>
                    <el-col :span="12" v-else-if="scope.row.service_check_state_code_id === '2'">
                        <el-button @click.native.prevent="on_resume_service_check_task(scope.$index, tableData)" type="warning" size="mini">恢复</el-button>
                    </el-col>
                    <el-col :span="12" v-else>
                        <el-button type="danger" size="mini">错误</el-button>
                    </el-col>
                </template>
            </el-table-column>
            <el-table-column
                    fixed="left"
                    :formatter="format_service_state"
                    prop="service_state_code_id"
                    label="服务运行状态"
                    width="120">
                <template slot-scope="scope">
                    <a><i :class="'iconfont icon-dian '+format_service_state_style(scope.row,'service_state_code_id')"></i>{{"    " + format_service_state(scope.row)}}</a>
                </template>
            </el-table-column>
            <el-table-column
                    :formatter="format_service_check_state"
                    prop="service_check_state_code_id"
                    label="服务检查状态"
                    width="120">
                <template slot-scope="scope">
                    <a><i :class="'iconfont icon-dian '+format_service_check_state_style(scope.row,'service_check_state_code_id')"></i>{{"    " + format_service_check_state(scope.row)}}</a>
                </template>
            </el-table-column>

            <el-table-column
                    prop="service_check_count"
                    label="检查累计"
                    width="80">
            </el-table-column>
            <el-table-column
                    prop="service_check_error_count"
                    label="错误累计"
                    width="80">
            </el-table-column>
            <el-table-column
                    prop="service_check_continued_error_count"
                    label="连续错误数"
                    width="90">
            </el-table-column>
            <el-table-column
                    prop="service_check_last_result"
                    label="最新检查结果"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="updateTime"
                    label="最新检测时间"
                    width="160">
            </el-table-column>
            <el-table-column
                    label="检查频率"
                    width="80"
            >
                <template slot-scope="scope">
                    <el-input size="mini" v-model="scope.row.service_check_rate" placeholder="请输入内容" @change="handleEdit(scope.$index, scope.row)"></el-input>
                    <span>{{scope.row.service_check_rate}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    :show-overflow-tooltip="true"
                    :formatter="format_service_ips"
                    prop="service_ips"
                    label="服务ip"
                    width="220"
            >
            </el-table-column>
            <el-table-column
                    prop="service_port"
                    label="服务端口"
            >
            </el-table-column>
        </el-table>
        <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="searchFormData.pageNum"
                :page-size="searchFormData.pageSize"
                :total="searchFormData.pageCount"
                :page-sizes="[12, 20, 50, 100]"
                layout="total, sizes, prev, pager, next, jumper"
        >
        </el-pagination>
    </el-dialog>
</template>
<script>
    export default {
        name: 'serviceCheckInfo',
        data() {
            return {
                module_name: 'serviceCheck',
                refresh_page_method: 'refresh_' + this.module_name,
                service_state_code_list: null,
                service_check_state_code_list: null,
                pageData: {},
                tableData: [],
                searchFormData: {
                    pageNum: 1,
                    pageSize: 12,
                    pageCount: 0
                },
            }
        },
        computed: {
            pageInfo() {
                return this.$setPageInfo(this.module_name)
            }
        },
        watch: {
            pageData() {
                this.search();
            }
        },
        async mounted() {
            this.service_state_code_list = await this.$setCodeList(this.service_state_code_list, 'service_state');
            this.service_check_state_code_list = await this.$setCodeList(this.service_check_state_code_list, 'service_check_state');
        },
        methods: {
            handleSizeChange(val) //修改单页显示条数
            {
                this.searchFormData.pageSize = val;
                this.search();
            },
            handleCurrentChange(val) //当前页修改
            {
                this.searchFormData.pageNum = val;
                this.search();
            },
            onSearch() // 查询按钮
            {
                this.searchFormData.pageNum = 1;
                this.search();
            },
            async search() {
                let searchData = this.searchFormData;
                if (this.pageInfo.pageData != null && this.pageInfo.pageData.hasOwnProperty('_id'))
                    searchData['service_project_id'] = this.pageInfo.pageData._id;
                let data = await this.$search(searchData, 'OAMP_search_service_check_list');
                this.searchFormData.pageCount = data.pageCount;
                this.tableData = data.data;
            },
            format_service_state(row, column, cellValue)/*格式化服务状态信息*/ {
                return this.$formatCodeValue(row, this.service_state_code_list, 'service_state_code_id')
            },
            format_service_state_style(row,key){
                let code = row[key];
                if (code === '1')
                    return 'set-info';
                else if (code === '2')
                    return 'set-warning';
                else if (code === '0')
                    return 'set-danger';
                else return 'set-black';
            },
            format_service_check_state(row, column, cellValue)/*格式化服务状态信息*/ {
                return this.$formatCodeValue(row, this.service_check_state_code_list, 'service_check_state_code_id')
            },
            format_service_check_state_style(row,key){
                let code = row[key];
                if (code === '1')
                    return 'set-info';
                else if (code === '3' || code === '2')
                    return 'set-warning';
                else if (code === '0')
                    return 'set-danger';
                else return 'set-black';
            },
            format_service_ips(row, column, cellValue)/*格式化ip显示方式*/ {
                let str = '';
                for (let i = 0; row.service_ips && i < row.service_ips.length; i++)
                    str += '[' + row.service_ips[i] + ']'
                return str
            },
            async on_run_all_service_check_task()/*运行所有服务检查*/ {
                let item = {
                    service_project_id: this.pageInfo.pageData._id
                };
                let data = await this.$exec(item, 'OAMP_run_service_check_task');
                if (data != null && data.success === '1') {
                    this.$message.success('运行全部检查成功')
                } else
                    this.$message.warning('运行全部检查异常')
                this.search()
            },
            async on_recreate_service_check_task()/*重新生成服务检查*/ {
                let item = {
                    service_project_id: this.pageInfo.pageData._id,
                };
                let data = await this.$exec(item, 'OAMP_recreate_service_check');
                if (data != null && data.success === '1')
                    this.$message.success('重新生成检查服务成功');
                else
                    this.$message.warning('重新生成检查服务异常');
            },
            async on_run_service_check_task(index, rows)/*运行服务检查*/ {
                let rowData = rows[index];
                let item = {
                    service_project_id: this.pageInfo.pageData._id,
                    _id: rowData._id
                };
                let data = await this.$exec(item, 'OAMP_run_service_check_task');
                if (data != null && data.success === '1')
                    this.$message.success('运行检查成功')
                else
                    this.$message.warning('运行检查异常')
                this.search()
            },
            async on_pause_service_check_task(index, rows) /*暂停服务检查*/ {
                let item = rows[index];
                let data = await this.$exec(item, 'OAMP_pause_service_check')
                if (data != null && data.success === '1')
                    this.$message.success('暂停服务检查成功')
                else
                    this.$message.warning('暂停服务检查异常')
                this.search()
            },
            async on_resume_service_check_task(index, rows)/*恢复服务检查*/ {
                let item = rows[index];
                let data = await this.$exec(item, 'OAMP_resume_service_check')
                if (data != null && data.success === '1')
                    this.$message.success('恢复服务检查成功')
                else
                    this.$message.warning('恢复服务检查异常')
                this.search()
            },
            async handleEdit(index, row)/*修改服务检查*/ {
                let data = await this.$exec(row, 'OAMP_modify_service_check_info')
                if (data != null && data.success === '1')
                    this.$message.success('修改服务检查成功')
                else
                    this.$message.warning('修改服务检查异常')
                this.search()
            }
        }
    }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .tb-edit .el-input {
        display: none
    }

    .tb-edit .current-row .el-input {
        display: block
    }

    .tb-edit .current-row .el-input + span {
        display: none
    }
</style>


